import React from "react";
import PlatformList from "./PlatformList";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";

function ToolKitDesktop(chosenStatementIds) {
  return (
    <Container
      fluid
      id="toolkit-desktop"
      className="text-center toolkit-desktop"
    >
      <h2 className="mb-5">
        Your <b>digital communication toolkit:</b>
      </h2>
      <hr />
      <Row>
        <PlatformList
          chosenStatementIds={chosenStatementIds.chosenStatementIds}
        ></PlatformList>
      </Row>
    </Container>
  );
}

function ToolKitMobile(chosenStatementIds) {
  return (
    <Container fluid id="toolkit-mobile" className="text-center toolkit-mobile">
      <p>
        Your <b>digital communication toolkit:</b>
      </p>
      <div className="tools">
        <PlatformList
          mobile={true}
          chosenStatementIds={chosenStatementIds.chosenStatementIds}
        ></PlatformList>
      </div>
    </Container>
  );
}

export { ToolKitDesktop, ToolKitMobile };
