import React from "react";
import Survey from "../components/Survey";
import { useSearchParams } from "react-router-dom";
import { ToolKitDesktop, ToolKitMobile } from "../components/ToolKit";
export default function SurveyPage() {
  let [searchParams, setSearchParams] = useSearchParams();
  let statements = searchParams.get("statements");
  let chosenStatementIds;
  if (!statements) {
    chosenStatementIds = [];
  } else {
    chosenStatementIds = statements.split("--");
  }
  return (
    <div id="survey-page" className="slide-in-blurred-top ">
      <Survey chosenStatementIds={chosenStatementIds}></Survey>
      <ToolKitDesktop chosenStatementIds={chosenStatementIds}></ToolKitDesktop>
      <ToolKitMobile chosenStatementIds={chosenStatementIds}></ToolKitMobile>
    </div>
  );
}
