import React from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import { Link } from "react-router-dom";
class Platform extends React.Component {
  render() {
    if (this.props.mobile) {
      console.log(this.props.platform.img_name);
      return (
        <Col
          data-relevant-survey-statements={this.props.platform.relevant_survey_statements.join(
            "--"
          )}
          className="platform-container my-1"
          xs={6}
          md={4}
          lg={3}
        >
          <Link
            className="btn w-100 h-100"
            role="button"
            to={`/platform/${this.props.platform.id}`}
          >
            <Card className="platform w-100 p-1">
              <Card.Img
                variant="top"
                src={`${process.env.PUBLIC_URL}/images/${this.props.platform.img_name}`}
              />

              <Card.Body>
                <Card.Title className="mb-3">
                  {this.props.platform.display_name}
                </Card.Title>
              </Card.Body>
            </Card>
          </Link>
        </Col>
      );
    } else {
      return (
        <Col
          data-relevant-survey-statements={this.props.platform.relevant_survey_statements.join(
            "--"
          )}
          className="platform-container my-1"
          sm={6}
          md={4}
          lg={3}
        >
          <Card className="platform">
            <Card.Img
              variant="top"
              src={`${process.env.PUBLIC_URL}/images/${this.props.platform.img_name}`}
            />
            <Card.Body>
              <Card.Title className="mb-3">
                {this.props.platform.display_name}
              </Card.Title>
              <Button
                target="_blank"
                className="mx-3"
                href={this.props.platform.link}
              >
                Get Started
              </Button>
              <Link
                className="mx-3 btn btn-primary"
                role="button"
                to={`/platform/${this.props.platform.id}`}
              >
                More Info
              </Link>
            </Card.Body>
          </Card>
        </Col>
      );
    }
  }
}

export default Platform;
